import { notFound } from "next/navigation";

const getProductDetails = async (id: string) => {
  try {
    const res = await fetch(`http://localhost:3000/api/product/${id}`, {
      cache: "no-store",
    });

    if (!res.ok) return null;
    const data = await res.json();
    console.log("Fetched Product Details:", data); // ✅ Check API response
    return data;
  } catch (error) {
    console.error("Error fetching product details:", error);
    return null;
  }
};

export default async function ProductDetailPage({
  params,
}: {
  params: { category: string; subcategory: string; productId: string };
}) {
  const product = await getProductDetails(params.productId);
  if (!product) return notFound();

  return (
    <div className="container mx-auto p-4">
      <div className="flex gap-8">
        {/* ✅ Image with correct styling */}
        <img
          src={product.image}
          alt={product.name}
          className="w-1/3 h-96 object-cover rounded-lg"
        />
        <div className="flex-1">
          <h1 className="text-3xl font-bold">{product.name}</h1>
          <p className="text-xl text-gray-700 mt-2">${product.price}</p>
          <p className="text-sm text-gray-500 mt-4">
            {product.description || "No description available."}
          </p>
          {/* ✅ Add to Cart Button */}
          <button className="mt-6 bg-blue-600 text-white px-4 py-2 rounded">
            Add to Cart
          </button>
        </div>
      </div>
    </div>
  );
}
